   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr 3fr 3fr;
}

header {
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-items: center;
  min-width: 100%;
  background-color: #2c0703;
}

#search-input {
  width: 160px;
  height: 25px;
  border: solid 3px #b6465f;
  border-radius: 10px;
  font-family: poppins;
  padding: 5px;
  color: black;
  background-color: lightgrey;
}

#search-btn {
  height: 25px;
  width: 65px;
  border: none;
  color: white;
  border-radius: 10px;
  background-color: #b6465f;
  font-family: poppins;
}

.heading-text {
  color: white;
  font-family: "rubik scribble", Impact, Haettenschweiler, "Arial Narrow Bold",
    sans-serif;
  font-size: 3.5rem;
}

/* switch styling */

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch::before {
  position: absolute;
  content: "°F";
  font-size: 1.4rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: white;
  right: 80px;
  top: 7px;
}

.switch::after {
  position: absolute;
  content: "°C";
  font-size: 1.4rem;
  font-family: sans-serif;
  color: white;
  left: 80px;
  top: 7px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #da9f93;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #b6465f;
}

input:focus + .slider {
  box-shadow: 0 0 1px #b6465f;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/* Weather containers */

.current-weather {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.current-container,
.forecast-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
  min-width: 80%;
  min-height: 80%;
  justify-content: center;
  align-items: center;
  font-family: poppins;
  border: dashed 3px darkcyan;
  background-color: #ebd4cb;
  border-radius: 15px;
  padding: 20px;
}

.current-location-text,
.forecast-date-text {
  font-size: 1.8rem;
}

.current-country-text {
  font-size: 1.2rem;
}

.current-temp-text,
.forecast-temp-text {
  position: relative;
  font-size: 3rem;
  font-weight: 300;
}

.current-temp-text::after {
  position: absolute;
  content: "°";
}

.current-condition-text,
.forecast-condition-text {
  font-size: 1.3rem;
}

.forecast-range-container {
  display: flex;
  gap: 15px;
  font-size: 20px;
}
.forecast-weather {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
}
